.main-nav {
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    height: 72px;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  &__logo {
    display: block;
    width: auto;
    height: 100%;
  }
}

@media (min-width: 768px) {
  .main-nav {
    &__wrapper {
      height: 54px;
      padding-top: 8px;
      padding-bottom: 8px;
    }

    &__logo {
      & img {
        width: auto;
        height: 100%;
      }
    }
  }
}

@media (min-width: 900px) {
  .main-nav {
    &__wrapper {
      height: 72px;
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }
}

@media (min-width: 1280px) {
  .main-nav {
    &__wrapper {
      height: 90px;
      padding-top: 14px;
      padding-bottom: 14px;
    }
  }
}

@media (min-width: 1700px) {
  .main-nav {
    &__wrapper {
      height: 100px;
      padding-top: 16px;
      padding-bottom: 16px;
    }
  }
}
