.concept {
  &__content-wrapper {
    padding-top: 100px;
  }

  &__content {
    & h3 {
      margin: 0;

      font-family: "Info", "Arial", sans-serif;
      font-weight: 400;
      font-size: 18px;
      text-transform: uppercase;
    }
  }

  &__content p {
    margin: 30px 0 0;
  }
}

@media (min-width: 700px) {
  .concept {
    &__content-wrapper {
      padding-top: 70px;
    }

    &__content {
      & h3 {
        font-size: 16px;
      }
    }

    &__content p {
      margin: 20px 0 0;

      font-size: 14px;
      line-height: 26px;
    }
  }
}

@media (min-width: 900px) {
  .concept {
    &__content-wrapper {
      padding-top: 100px;
    }

    &__content {
      width: 855px;

      & h3 {
        font-size: 20px;
      }
    }

    &__content p {
      margin: 30px 0 0;

      font-size: 16px;
    }
  }
}

@media (min-width: 1100px) {
  .concept {
    &__content-wrapper {
      padding-top: 150px;
    }

    &__content {
      width: 970px;

      & h3 {
        font-size: 25px;
      }
    }

    &__content p {
      // margin: 30px 0 0;

      line-height: 21px;
    }
  }
}

@media (min-width: 1200px) {
  .concept {
    &__content {
      width: 1070px;
    }
  }
}

@media (min-width: 1400px) {
  .concept {
    &__content {
      width: 1170px;
    }
  }
}

@media (min-width: 1700px) {
  .concept {
    &__content-wrapper {
      padding-top: 310px;
    }

    &__content {
      width: auto;

      & h3 {
        font-size: 38px;
      }
    }

    &__content p {
      margin: 45px 0 0;

      font-size: 20px;
      line-height: 26px;
    }
  }
}

@media (min-width: 1920px) {
  .concept {
    &__content {
      width: 1620px;
    }
  }
}
